.projects__grid {
  max-width: 1100px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(18em, 1fr));
  grid-gap: 2em;
}

.project__item{
  transition: all 0.2s linear;
}

@media only screen and (max-width: 800px) {
  .projects__grid {
    justify-content: center;
  }
}